import React, { createContext, useState, useEffect, useContext } from 'react';
import axios from 'axios';

const AuthContext = createContext(null);

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // Check if user is logged in on mount
    const token = localStorage.getItem('token');
    console.log('AuthContext: Token on mount:', token);
    
    if (token) {
      // Set the auth header for axios
      axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
      fetchUserInfo();
    } else {
      setLoading(false);
    }
  }, []);

  const fetchUserInfo = async () => {
    try {
      console.log('AuthContext: Fetching user info...');
      const response = await axios.get('/api/auth/me');
      console.log('AuthContext: User info response:', response.data);
      setUser(response.data);
    } catch (error) {
      console.error('AuthContext: Error fetching user info:', error);
      console.error('AuthContext: Error response:', error.response?.data);
      localStorage.removeItem('token');
      delete axios.defaults.headers.common['Authorization'];
      setUser(null);
    } finally {
      setLoading(false);
    }
  };

  const login = async (email, password) => {
    try {
      console.log('AuthContext: Attempting login...');
      const response = await axios.post('/api/auth/login', { email, password });
      const { token, user } = response.data;
      console.log('AuthContext: Login response:', { token: !!token, user });
      
      // Store token
      localStorage.setItem('token', token);
      
      // Set axios default header
      axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
      
      // Set user data
      setUser(user);
      
      return { success: true };
    } catch (error) {
      console.error('AuthContext: Login error:', error);
      return { 
        success: false, 
        error: error.response?.data?.error || 'Login failed' 
      };
    }
  };

  const register = async (userData) => {
    try {
      console.log('AuthContext: Attempting registration...');
      const response = await axios.post('/api/auth/register', userData);
      console.log('AuthContext: Registration response:', response.data);
      
      // After successful registration, log the user in
      return await login(userData.email, userData.password);
    } catch (error) {
      console.error('AuthContext: Registration error:', error);
      return { 
        success: false, 
        error: error.response?.data?.error || 'Registration failed' 
      };
    }
  };

  const logout = () => {
    console.log('AuthContext: Logging out...');
    localStorage.removeItem('token');
    delete axios.defaults.headers.common['Authorization'];
    setUser(null);
  };

  const value = {
    user,
    login,
    logout,
    register,
    loading,
  };

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
};
